/**
 * **** card-group ****
 * .card-group 
 * .card-deck 
 * .card-columns 
 * .accordion
 *
 * **** card ****
 * .card  card
 * .card-info card-* header card-*header
 * .card-outline card-infoheaderborder,header
 * .card-outline-tabs headera card divclassaaborder
 * .card-tabs
 * .text-light 
 * .bg-* cardheadercard-*
 * .bg-gradient-* 
 * .height-control body300px
 *
 * **** card-img ****
 * .card-img 
 * .card-img-top top
 * .card-img-buttom buttom
 * body,header,footer.card-img-overlaycard-img-overlay
 *
 * **** card-body ****
 * .card-body 
 * .bg-*
 * .card-img-overlay card-img
 * .text-light 
 *
 * **** card-header ****
 * .card-header 
 * .bg-*
 * .card-img-overlay card-img
 * .text-light 
 *
 * **** card-tool ****
 * .card-tool 
 * .card-toolheaderheader
 *
 * card-toolbtn-group showcard-*
 * btn-group show
 *
 * **** card-footer ****
 * .card-footer 
 * .bg-*
 * .card-img-overlay card-img
 * .text-light 
 *
 * **** card-title ****
 * .card-title 
 * .text-light 
 *
 * **** card-subtitle ****
 * .card-subtitle 
 * .text-light 
 *
 * **** card-text ****
 * .card-text 
 * .text-light 
 *
 * **** card-link ****
 * .card-link link
 *
 * **** card-comments ****
 * bg-*
 * text-light
 *
 * **** card-comments-img
 * img-circle
 *
 * **** comment-text ****
 * text-light
 *
 * **** card-comments-username
 * text-light
 *
 */

import Vue from "../../utils/vue";
import {
  bgGradientOptions,
  cardVariantOptions,
  textVariantOptions,
  bgVariantOptions
} from "../../utils/nly-config";
import { nlyGetOptionsByKeyEqual } from "../../utils/get-options";
import { NlyIcon } from "../icon/icon";
import { NlyOverlay } from "../overlay/overlay";
import { nlyCardId } from "../../utils/mixin-id";

const name = "NlyCard";

export const NlyCard = Vue.extend({
  name: name,
  props: {
    headerVariant: {
      type: String
    },
    headerOutline: {
      type: Boolean,
      default: false
    },
    cardOutlineTabs: {
      type: Boolean,
      default: false
    },
    cardTabs: {
      type: Boolean,
      default: false
    },
    textVariant: {
      type: String
    },
    bgVariant: {
      type: String
    },
    bgGradientVariant: {
      type: String
    },
    heightControl: {
      type: Boolean,
      default: false
    },
    cardClass: {
      type: String
    },
    loading: {
      type: Boolean,
      default: false
    },
    loadingContent: {
      type: String
    },
    loadingContentTag: {
      type: String,
      default: "p"
    },
    loadingContentClass: {
      type: String
    },
    loadingIcon: {
      type: String,
      default: "fas fa-2x fa-sync-alt fa-spin"
    },
    loadingImgSrc: {
      type: String
    },
    loadingImgClass: {
      type: String
    },
    tag: {
      type: String,
      default: "div"
    },
    dark: {
      type: Boolean,
      default: false
    },
    id: {
      type: String
    }
  },
  computed: {
    customProps: function() {
      return {
        headerVariant: nlyGetOptionsByKeyEqual(
          cardVariantOptions,
          this.headerVariant
        ),
        headerOutline: this.headerOutline ? "card-outline" : "",
        cardOutlineTabs: this.cardOutlineTabs ? "card-outline-tabs" : "",
        cardTabs: this.cardTabs ? "card-tabs" : "",
        textVariant: nlyGetOptionsByKeyEqual(
          textVariantOptions,
          this.textVariant
        ),
        bgVariant: nlyGetOptionsByKeyEqual(bgVariantOptions, this.bgVariant),
        bgGradientVariant: nlyGetOptionsByKeyEqual(
          bgGradientOptions,
          this.bgGradientVariant
        ),
        heightControl: this.heightControl ? "height-control" : "",
        cardClass: this.cardClass,
        loading: this.loading,
        loadingContent: this.loadingContent,
        loadingContentTag: this.loadingContentTag,
        loadingContentClass: this.loadingContentClass,
        loadingIcon: this.loadingIcon,
        loadingImgSrc: this.loadingImgSrc,
        loadingImgClass: this.loadingImgClass,
        tag: this.tag,
        dark: this.dark,
        id: nlyCardId(this.id)
      };
    }
  },
  render(h) {
    const overlayArray = () => {
      if (this.customProps.loadingContent) {
        return h(
          NlyOverlay,
          {
            props: {
              dark: this.customProps.dark
            }
          },
          [loadingContentArray()]
        );
      } else {
        return h(
          NlyOverlay,
          {
            props: {
              dark: this.customProps.dark
            }
          },
          [loadingIcon()]
        );
      }
    };

    const loadingContentArray = () => {
      return h(
        this.customProps.loadingContentTag,
        {
          class: [this.customProps.loadingContentClass]
        },
        this.customProps.loadingContent
      );
    };

    const loadingIcon = () => {
      return h(NlyIcon, {
        props: {
          icon: this.customProps.loadingIcon
        }
      });
    };

    const loadingImgArray = () => {
      return h("img", {
        attrs: {
          alt: "loading-img",
          src: this.customProps.loadingImgSrc
        },
        staticClass: "loading-img",
        class: [this.customProps.loadingImgClass]
      });
    };

    const loadingArray = () => {
      if (this.customProps.loading && !this.customProps.loadingImg) {
        return [this.$slots.default, overlayArray()];
      } else if (this.customProps.loading && this.customProps.loadingImg) {
        return [this.$slots.default, loadingImgArray()];
      } else {
        return [this.$slots.default];
      }
    };
    return h(
      this.customProps.tag,
      {
        attrs: {
          id: this.customProps.id
        },
        staticClass: "card",
        class: [
          this.customProps.headerVariant,
          this.customProps.headerOutline,
          this.customProps.cardOutlineTabs,
          this.customProps.cardTabs,
          this.customProps.textVariant,
          this.customProps.bgVariant,
          this.customProps.bgGradientVariant,
          this.customProps.heightControl,
          this.customProps.cardClass
        ]
      },
      [loadingArray()]
    );
  }
});
