// 
// 

import { isArray, isUndefined } from "../utils/inspect";

export default {
  methods: {
    hasListener(name) {
      //  v-on:
      const $listeners = this.$listeners || {};
      //  `v-on:name` and `this.$on('name')`

      const $events = this._events || {};

      return (
        !isUndefined($listeners[name]) ||
        (isArray($events[name]) && $events[name].length > 0)
      );
    }
  }
};
